import "../styles/about.css";
import ScrollTrigger from "react-scroll-trigger";
import { useState } from "react";
import { NavLink } from "react-router-dom";

/*Import all team member images - GET PERMISSION*/
import vivian from "../img/about-viv.jpg";
import fatema from "../img/about-fatema.jpg";
import brittney from "../img/about-britt.jpg";
import habiba from "../img/about-habiba.jpg";

const teamMembers = [
  { name: "Vivian Ha", role: "President", img: vivian },
  { name: "Fatema Chowdhury", role: "Co-President", img: fatema },
  { name: "Habiba", role: "...", img: habiba },
  { name: "Brittney Ha", role: "VP of Technology", img: brittney }
];

const About = () => {
  return (
    <div className="about-page">
      <h1 className="about-title">Meet the Team</h1>
      <div className="team-grid">
        {teamMembers.map((member, index) => (
          <div key={index} className="team-card">
            <div className="card-inner">
              {/* Front */}
              <div className="card-front">
                <img src={member.img} alt={member.name} />
                <h3>{member.name}</h3>
                <p>{member.role}</p>
              </div>

              {/* Back */}
              <div className="card-back">
                <p>{member.bio}</p>
                <a href={member.linkedIn} target="_blank" rel="noreferrer">
                  LinkedIn Profile
                </a>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default About;
